<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'
import { getRoles } from 'data/listBody'

const dataOption = {
  leftMenu: [
    { name:'用户姓名：', placeholder:'请输入用户姓名', module:'name'},
    { name:'所属角色：', placeholder:'请选择所属角色', module:'role', type:'select'},
  ],
  rightMenu: [
    { id:'r1', name:'查询'},
    { id:'r2', name:'新增'}
  ]
}

const dataTable = {
  table: [
    { label:'用户ID', prop:'id'},
    { label:'用户姓名', prop:'name'},
    { label:'所属角色', prop:'role'},
  ],
  edit: [
    {name: '邀请列表', id: 't3'},
    {name: '编辑', id: 't1'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'systemUser',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.dropInit();
    this.search();
  },
  methods:{
    dropInit(){ // 数据初始化
        getRoles((res) => {
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
                if(item.module === 'role') item.options = res;
                return item;
            })
            this.dataOption.leftMenu = leftMenu;
        })
    },
    optchange(params){
      console.log(params.id + '--id被单击了')
      if(params.id === 'r1') return this.search();
      if(params.id === 'r2') return this.$router.push(`/systemManage/userAdd?type=new`);
    },
    tabchange(params){
      console.log(params.id + '--id被单击了')
      this.id = params.data.id;
      // 编辑
      if(params.id === 't1') return this.$router.push(`/systemManage/userAdd?type=edit&id=${params.id}`);
      // 删除
      if(params.id === 't2') return this.dialogVisible = true;
      // 邀请列表      
      if(params.id === 't3') return this.$router.push(`/systemManage/userInvite?type=new`);
    },
    sure(){
      this.search();
      this.dialogVisible = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };

      const params = Object.assign({}, optParams, pageParams)
      this.tableList = [
        {id: '1', name: '张三1', role: '管理员', price: '1200', time: '2018-01-10--2015-02-10'},
        {id: '2', name: '张三2', role: '超级管理员', price: '1200', time: '2018-01-10--2015-02-10'},
        {id: '3', name: '张三3', role: '教师', price: '1200', time: '2018-01-10--2015-02-10'},
      ]
      // this.loading = true;
      // const param = queryString.stringify(params || {});
      // let url = urls[code];
      // this.api.get(`/role`).then((res)=>{
        // this.loading = false;
        // this.data = res.data.list;
        // this.setPage(res.data)
        // this.dataInit();
      // }).catch((err) => {
      //   console.log(err)
      //   this.$message(err.message)
      // })
    },
    dataInit(){//数据初始化

    }
  }
}
</script>
 
